@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);

.headerRe, .backToUser{
  color: white;
  text-shadow: 3px 2px  #00000033;  
}

.headerRe{
  background-color: #608CA7;
  margin: 0;
  width: 100vw;
  padding: 3vh 0;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  position: fixed; 
  top: 0; 
  z-index: 99999999;
}

.headerRe h3{
  margin: 1vh;
  display: inline;
}

.itemContainer{
  width: 96vw;
  margin: 1.5vh 0.8vh;
  padding: 1.5vh 0vh;
  /* height: 7vh; */
  color: #556b77;
  border-radius: 0.5vh;
  box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 8px 0 rgba(0, 0, 0, 0.19);  
}

.itemContainer:last-child{
  margin-bottom: 10vh;
}

.itemContainer:nth-child(odd){
  background-color: #ebe5e5; 
}

.footer{
  margin-bottom: 0;
  /* background-color: #608CA7; */
}

.footerIcon{
  color: white;
  font-size: 22px;
  margin: 1.5vh;
}

.containerFooter{
  background-color: #608CA7;
}


.App {
  text-align: center;
  /* background-image: linear-gradient(#27b09c,#8b40bf); */
  /* background-image: linear-gradient(#8b40bf, #27b09c); */
  height: 100vh;
}
.login{
  background-color: #556b77;
  height: 100vh;
  width: 100vw;
  vertical-align: middle;
  position: absolute;
}

.jumbotron{
  position: relative;
  margin: 10vh 6vw 0 6vw;
  border-radius: 2vw;
}

.labelLogin{
  display: block;
  text-align: left;
  margin-left: 14vw;
}

.enterData{
  width: 70vw;
  margin: 1vh 0;
  padding: 1vh 0;
}

.remember{
  text-align: left;
}

.remember input:checked ~ .checkmark{
  background-color: red;
}
.contCard{
  /* background-image: linear-gradient(#27b09c,#8b40bf);  */
  background-color: #608CA7; 
  padding: 2vh 0 15vh 0;  
}

.card{
  margin: auto;
  width: 295px;
  height: 166px;
  border-radius: 1vh;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
  background-image: url(/finciero/static/media/max_bg.a955cffb.png);
  background-repeat: no-repeat;
  background-size: 120%;
  color: white;
}

.data{
font-family: 'Roboto Mono', monospace;  
}

.money{
  margin: 1vh 0 0 0;
}

.contCard h5{
  text-shadow: 2px 2px black;
}

.hideData{
  background-color: #27b09c;
  margin-top: 5.5vh;
}

.hideData p{
  margin: 0;
  font-weight: bold;
}
.menuTitle{
  text-align: left;
  font-size: 16px;
  color: #556b77;  
}

.menuItem{
  margin: 1vh;
  padding: 1.5vh;
  height: 7vh;
  background-color: lightgrey;
  border-radius: 0.5vh;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 4px 0 rgba(0, 0, 0, 0.19);    
}

.menuIcon{
  font-size: 22px;
  color: #556b77;
}
.navTop{
  margin-top: 8vh;
}

.navbar-default{
  margin-bottom: 0;
  box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 8px 0 rgba(0, 0, 0, 0.19);    
}

.navbar-brand{
  padding: 0.2vh 0 0 5vw;
}

.navbar-nav>li{
  display: inline-block;
}

.nav>li>a{
  display: inline-block;
}

.contCard>img{
  margin: auto;
}

.photoUser{
  border-radius: 50%;
  width: 3em;
  margin-top: 0.5vh;
  margin-left: -4vw;  
}

.languajeBtn{
  margin-top: 1.2vh;
  margin-left: -4vw;
}

.envelope{
  font-size: 4vh;
  margin-top: 1.2vh;
  margin-left: -2vw;  
  color: #556b77;
}
.user{
  height: 84vh;
}
.recharge{
  height: 84vh;
  width: 100vw;
}

.recharge .row, .rechargeMoney .row{
  margin: 0;  
}

.recharge h3{
  margin: 1vh;
  display: inline;
}

.recharge h4{
  color: #8b40bf;
  font-weight: bold;
}

.rechargeOp{
  text-align: left;
  font-size: 16px;
  color: #556b77;
  font-weight: bold;
}

.quantity{
  background-color: #e4e0e0;
  margin: 2vh 1.5vh 0 1.5vh;
  box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 8px 0 rgba(0, 0, 0, 0.19);    
}

.quantity h2{
  color: #556b77;
  text-shadow: 1px 1px lightgray;
}

.quantity input{
  border-top: #556b77;
  border-left: none;
  border-right: none;
  width: 55vw;
  border-radius: 1vh;
  background-color: white;  
  margin: 4vh 0;
  padding-left: 1vh;
  box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 8px 0 rgba(0, 0, 0, 0.19);  
}

.quantity input::-webkit-input-placeholder{
  color: gray;
  font-size: 14px;
}

.quantity input::-moz-placeholder{
  color: gray;
  font-size: 14px;
}

.quantity input:-ms-input-placeholder{
  color: gray;
  font-size: 14px;
}

.quantity input::-ms-input-placeholder{
  color: gray;
  font-size: 14px;
}

.quantity input::placeholder{
  color: gray;
  font-size: 14px;
}

.quantity button{
  margin: 14vh 0  4vh 0;
  font-size: 22px;
  background-color: #449faf;
}

.instructionsCont{
  margin: 3vh auto;
  border-radius: 1vh;
  width: 90vw;
  box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 8px 0 rgba(0, 0, 0, 0.19);
  text-align: justify;
}

.record{
  color: #449faf;
  vertical-align: middle;
}

.instructions{
  text-align: left;  
  font-size: 14px;
  color: #556b77;
  font-weight: bold;
}

.back{
  font-size: 22px;
  color: white;
  vertical-align: middle;
  margin-top: 1.2vh;
}

.mailFinciero{
  color: #449faf;
  font-weight: bold;
}

.uploadTicket{
  font-size: 30px;
  color: #449faf;
}

input[type="file"] {
  display: none;
}
.rechargeHistory{
  line-height: 12px;
  text-align: justify;
}

.recharge:first-child{
  margin-top: 11vh;  
}

.date{
  color: #8b40bf;
}

.total{
  color: #27b09c;
  font-weight: bold;
}

.glyphicon-remove-sign{
  color: #e40000;
}

.glyphicon-ok-sign{
  color: #0b920b;
}

.glyphicon-minus-sign{
  color: gray;  
}

.glyphicon-ok-sign, .glyphicon-remove-sign, .glyphicon-minus-sign{
  font-size: 18px;
}

hr{
  margin: 0.5vh;
  background-color: gray;
}
.comCont{
  background-color: lightgray;
  margin: 1.2vh 0 0 -1.2vh;
  width: 46vw;
  height: 46vw;
  border-radius: 2vh;
  position: relative;
}

.commerceCont:last-child{
  margin-bottom: 10vh;
}

.comCont img{
  width: 40vw;
  padding: 1.5vh 0;
  position: absolute;
  left: 50%; top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 20%;  
  /* filter: grayscale(50%); */
}

.modalCont{
  margin-top: 10vh !important;
}
.selectTransactions{
  font-weight: bold;
  color: #556b77;  
  width: 96vw;
  margin: 1.5vh 0.8vh;
  padding: 1.5vh 0vh;
  margin-top: 10vh;  
}

.selectTransactions p{
  padding: 1vh 0 0 1vh;  
}

.selectTransactions select{
  border-radius: 1vh;
  padding: 1.1vh;
  color: #556b77;    
  margin: 1vh;
}

.titleTransactions{
  background-color: #449faf;
  width: 96vw;
  margin: 1.5vh 0.8vh;
  padding: 1vh 0 0 0;
  /* height: 7vh; */
  border-radius: 0.5vh;
  box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 8px 0 rgba(0, 0, 0, 0.19); 
  color: white;
  font-weight: bold;
  font-size: 16px;
}

.amount{
  color: #8b40bf
}

.selectTransactions button{
  background-color: #449faf;
}
.profileCont{
  text-align: justify;
  background-color: #ebe5e5;   
  
}

.profileItem{
  width: 90vw;
  margin: 0 auto 1.5vh auto;
  padding: 0.8vh;
  /* height: 7vh; */
  color: #556b77;
  border-radius: 0.5vh;
  box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 8px 0 rgba(0, 0, 0, 0.19);  
  color: #449faf;
  font-weight: bold;
  background-color: white;   
}

.profileCont label, .profileItem{
  text-align: justify;
  margin: 0 auto 1.5vh auto;
  display: block;  
}

.profileCont label{
  margin: 0 0 0 4vh;
  color: #556b77; 
}

a{
  color: #8b40bf;
}

.contact{
  margin-bottom: 10vh;
}

input{
  border: none;
}

.sendData{
  margin: 1vh auto;
  display: block;

}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

